.Navbar-name{
    color: #fff;
}
.navbar{
    background-color: rgb(94, 76, 130);
}
.nav-all-link{
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 0;
    padding: 0;
}
.section{
    position: relative;
  /* animation: mymove 5s; */
  animation-fill-mode: forwards;
}
.section{
    animation: rightThenLeft 2s ease-in-out;
}
@keyframes rightThenLeft {
    from {left:200px;}
  to {left: 0px;}
}